.toolbar{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;background-color:#4aa3a3;height:51px;padding-left:0.3rem;border:1px solid #1d2f2f;box-shadow:1px 1px 10px 2px #3a5f5f;font-family:Russo One;font-size:1rem;color:black}.icono{float:right;margin:0;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:50px;min-height:5px;height:50px}

.ajuste{display:block;font-size:0.879rem;font-family:monospace;padding:5px}.width-ajuste{min-height:200px}

.negro{color:black}code{background-color:white;padding-left:5px;padding-right:5px}pre{background:white;overflow:auto;display:block;padding-top:5px;padding-left:10px;padding-right:10px;line-height:1.2}pre code{margin:1px;font-size:0.875rem;font-weight:bold}.ajustes{line-height:1.7}.border-1{border-bottom:10px solid #224b4b}.border-2{border-bottom:5px solid #224b4b}h3{line-height:1.5 !important;margin-top:15px !important;font-size:calc(1.1rem + .6vw) !important}blockquote{border-left:3px solid #224b4b;color:#224b4b;padding-left:5px;margin-left:25px}table,th,td{border-collapse:collapse;border:2px solid #224b4b}table th,th th,td th{font-size:calc(0.9rem + .6vw) !important;text-align:center}table td,th td,td td{font-size:calc(0.6rem + .6vw) !important}p{font-size:calc(0.6rem + .6vw) !important}li{font-size:calc(0.8rem + .6vw) !important}

